import React from 'react'
import Carousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";

const responsive = {
    desktop: {
        breakpoint: { max: 3000, min: 1024 },
        items: 3,
        slidesToSlide: 3 // optional, default to 1.
    },
    tablet: {
        breakpoint: { max: 1024, min: 464 },
        items: 1,
        slidesToSlide: 1 // optional, default to 1.
    },
    mobile: {
        breakpoint: { max: 464, min: 0 },
        items: 1,
        slidesToSlide: 1 // optional, default to 1.
    }
};
export default function Carousal({ config }) {
    return (
        <Carousel
            ssr
            partialVisbile
            itemClass="image-item"
            responsive={responsive}
        // partialVisbile
        // swipeable={true}
        // draggable={true}
        // showDots={true}
        // responsive={responsive}
        // ssr={false} // means to render carousel on server-side.
        infinite={true}
        // autoPlay={true}
        // autoPlaySpeed={1000}
        keyBoardControl={true}
        // customTransition="all 1"
        // transitionDuration={500}
        // containerClass="carousel-container"
        // // removeArrowOnDeviceType={["tablet", "mobile"]}
        // // deviceType={this.props.deviceType}
        // dotListClass="custom-dot-list-style"
        // itemClass="carousel-item-padding-40-px"
        >
            {
                [...new Array(6)].map((item, i) => (
                    <CarousalData config={config} />
                ))
            }
        </Carousel>
    )
}

export const CarousalData = ({ config }) => {
    return React.createElement(
        config.element.type,
        {
            className: `${config.css}`,
            src: config.data && config.data.src && config.data.src
        },
        config.children ?
            config.children.map((c) => <CarousalData config={c} />) : config.data && config.data.value
    );
}
