import React, { createContext, useEffect, useState } from "react";

export const UserContext = createContext(null);


export const Context = ({ children }) => {
const [value, setValue] = useState("My name is Divyansh")
    return (
        <UserContext.Provider
            value={{
                value, setValue
            }}
        >
            {children}
        </UserContext.Provider>
    );
};