import { Route, Routes, resolvePath } from 'react-router-dom';


import { Suspense, lazy, useEffect, useState } from "react";


const blogEntries = [
    {
        path: "/",
        comp: "JsonComponent"
    },
    {
        path: "/About",
        comp: "Another"
    },
    {
        path: "/product/:id",
        comp: "Product"
    },

]

const JsonComponent = []
blogEntries.map((name) => {
    JsonComponent.push({
        path: name.path,
        comp: lazy(() => import(`./${name.comp}.js`))
    })
})
const Data = JsonComponent[0]

function App() {

    return (
        <Routes>
            {
                JsonComponent.map((Data) => (
                    <Route path={Data.path} element={
                        <Suspense fallback={<p>Loading.....</p>}>
                            <Data.comp />
                        </Suspense>
                    } />
                ))
            }
        </Routes>
    );
}

export default App;


